<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, onMounted, watch } from "vue";
import { useStore } from "vuex";
import config from "@/utils/config";
import { Toast } from 'vant';
import Clipboard from 'clipboard'
import { useI18n } from "vue-i18n"
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      config: config,
      initContract: () => {
        data.initData()
      },
      toBuy: () => {
        window.location.href = `https://pancakeswap.finance/swap?inputCurrency=${config.usdtAddress}&outputCurrency=${config.freeDomAddress}`
      },
      initData: () => {
      },
    })
 
    if (data.address) {
      data.initContract()
    }
     onMounted(() => {
      var clipboard = new Clipboard('.copy-btn')
      clipboard.on('success', function (e) {
        Toast(t('index.复制成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('index.复制失败，请手动复制'))
      })
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
